
import { computed, defineComponent } from 'vue'

import {
  NImage,
  NThing,
  NList,
  NListItem,
  NButton,
  NIcon,
  NSpace,
  NInputNumber
} from 'naive-ui'
import { Times } from '@vicons/fa'

import { useCart } from '@/store/cart'

export default defineComponent({
  name: 'CartList.vue',

  props: {},

  components: {
    Times,
    NImage,
    NThing,
    NList,
    NListItem,
    NButton,
    NIcon,
    NSpace,
    NInputNumber
  },

  setup() {
    const cart = useCart()

    const basket = computed(() => cart.basket)

    const remove = (index: number) => cart.removeItem(index)

    return {
      cart,
      basket,
      remove
    }
  }
})
